.badge {
    background: #ffffff;
    box-shadow: 0 4px 8px 0 rgba(0, 0, 0, 0.5);
    border-radius: 8px 8px 8px 8px;
    overflow: hidden;
    height: 380px;
    width: 100%;
}

.badge_header {
    height: 100px;
    background: #1b1b25;
    display: flex;
    justify-content: center;
}

.badge_section-name {
    display: flex;
    justify-content: center;
    align-items: center;
    padding: 1rem 0;
}

.badge_section-info {
    display: flex;
    flex-direction: column;
    justify-content: center;
    align-items: center;
    padding: 0.5rem 0;
    background: #f4f4f7;
}

.badge_avatar {
    border-radius: 50%;
    margin-right: 1rem;
    width: 120px;
    height: 120px;
    background-position: center;
    background-size: cover;
}

.badge_footer {
    height: 54px;
    display: flex;
    justify-content: center;
    align-items: center;
    color: #98ca3f;
    font-weight: bold;
    font-style: italic;
}

.badge_twitter {
    color: #00acee;
}