.Badges__hero {
    width: 100%;
    padding: 2rem 0;
    background: url('../../img/stars.svg'), #1B1B25;
    background-repeat: repeat;
    margin-bottom: 1rem;
}

.Badges__hero>.Badges__container {
    display: flex;
    flex-direction: column;
    align-items: center;
}

.Badges__container {
    width: 100%;
    max-width: 580px;
    margin: 0 auto;
    padding: 0 1rem;
}

.Badges_conf-logo {
    margin-bottom: 2rem;
}

.Badges__buttons {
    display: flex;
    width: 100%;
    max-width: 580px;
    padding: 0 1rem;
    justify-content: flex-end;
    margin: 0 auto;
    margin-bottom: 1rem;
}

.Badges__buttons a {
    transition: all .2s;
}

.Badges__buttons a:hover {
    transform: scale(1.1);
}